<template>
  <div class="app-cell" @click="onClick" :class="{ border }">
    <div class="app-cell-title">
      {{ title }}
      <slot name="title"></slot>
    </div>
    <div class="app-cell-value" :class="{ placeholder: placeholder && !value }">
      {{ value || placeholder }}

      <div v-if="label" class="app-cell-label">
        {{ label }}
      </div>
    </div>
    <div class="app-cell-right">
      <slot name="right"></slot>
      <van-icon name="arrow" v-show="isLink" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppCell',
  props: {
    isLink: {
      type: Boolean,
      default: true,
    },
    border: {
      type: Boolean,
      default: false,
    },

    title: {
      type: String,
      default: '',
    },

    value: {
      type: String,
      default: '',
    },

    label: {
      type: String,
      default: '',
    },

    placeholder: {
      type: String,
      default: '',
    },
  },

  methods: {
    onClick() {
      this.$emit('click');
    },
  },
};
</script>

